<template>
  <div id="cheque-scan">
    <qrcode-stream @decode="onDecode" :track="paintOutline" @init="onInit"/>
    <div class="desc">{{ $store.state.lang === 'ru' ? 'Только у нас ты можешь повторно использовать свои чеки от приложения soliq.uz и получить совершенно бесплатно 2 билета.' : 'Faqat bizda soliq.uz ilovasidagi cheklaringizdan qayta foydalanishingiz va 2 ta chiptani mutlaqo bepul olishingiz mumkin.'}}</div>
    <button class="cancel-scan" @click="$router.push('/')">{{ $store.state.langlang === 'ru' ? 'Отменить' : 'Bekor qilish' }}</button>
  </div>
</template>

<script>
import { QrcodeStream } from 'vue-qrcode-reader'
import axios from "axios";
export default {
  name: "QrCodeCashBack",
  data: () => ({
    scanResult: '',
  }),
  components: {QrcodeStream},
  async mounted() {
    Telegram.WebApp.BackButton.show()
    Telegram.WebApp.BackButton.onClick(() => {
      this.$router.push('/')
    });

  },
  methods: {
    onDecode (result) {
      this.scanResult = result
      axios.post('https://server.gold-ticket.uz/api/soliq/' + this.$store.state.user_id, {
        url: result
      })
      .then(response => {
        Telegram.WebApp.showAlert(response);
      });
    },
    async onInit(promise) {
      try {
        await promise
      } catch (error) {
        this.$router.push('/');
        if (error.name === 'NotAllowedError') {
          Telegram.WebApp.showAlert("ERROR: you need to grant camera access permission")
        } else if (error.name === 'NotFoundError') {
          Telegram.WebApp.showAlert("ERROR: No camera found on your device!")
        } else if (error.name === 'NotSupportedError') {
          Telegram.WebApp.showAlert("ERROR: secure context required (HTTPS, localhost)")
        } else if (error.name === 'NotReadableError') {
          Telegram.WebApp.showAlert("ERROR: is the camera already in use?")
        } else if (error.name === 'OverconstrainedError') {
          Telegram.WebApp.showAlert("ERROR: installed cameras are not suitable")
        } else if (error.name === 'StreamApiNotSupportedError') {
          Telegram.WebApp.showAlert("ERROR: Stream API is not supported in this browser")
        } else if (error.name === 'InsecureContextError') {
          Telegram.WebApp.showAlert('ERROR: Camera access is only permitted in secure context. Use HTTPS or localhost rather than HTTP.')
        } else {
          Telegram.WebApp.showAlert(`ERROR: Camera error (${error.name})`)
        }
      }
    },
    paintOutline (detectedCodes, ctx) {
      for (const detectedCode of detectedCodes) {
        const [ firstPoint, ...otherPoints ] = detectedCode.cornerPoints
        ctx.strokeStyle = "red";
        ctx.beginPath();
        ctx.moveTo(firstPoint.x, firstPoint.y);
        for (const { x, y } of otherPoints) {
          ctx.lineTo(x, y);
        }
        ctx.lineTo(firstPoint.x, firstPoint.y);
        ctx.closePath();
        ctx.stroke();
      }
    },
  },
  watch: {
    scanResult(value) {
      Telegram.WebApp.showPopup({
        'title': 'Кешбек',
        'message': 'Чек успешно зарегистрирован',
        'buttons': [
          {
            'id': 'success',
            'type':'ok',
            'text': "Успешно"
          }
        ]
      })
      Telegram.WebApp.onEvent('success', () => {
        this.$router.push('/')
      })
    },
  }
}
</script>

<style scoped>
@import url('../style/cheque-scan.css');
</style>
